import { graphql, useStaticQuery } from "gatsby"
import * as React from "react"

const AppFooter = () => {
  const data_ = useStaticQuery(graphql`
  query FooterQuery {
    site {
      siteMetadata {
        author
        title
        nav {
          text
          to
        }
      }
    }
  }
`)
  return (
    <div className="w-[98%] md:w-4/5 select-none lg:w-3/5 left-0 right-0 m-auto relative mt-3">
      <div className="flex flex-col items-center justify-center mb-4 h-12 rounded-xl">
        <div className="flex flex-row gap-3">
        <div className="font-bold transition-all">{data_.site.siteMetadata.title}</div>
        <div className="font-bold transition-all">{data_.site.siteMetadata.author}</div>
        <div className="font-bold transition-all" onClick={() => window.scrollTo(0, 0)}>回到顶部</div>
        </div>
      </div>
    </div>
  )
}

export default AppFooter