import * as React from "react"
import { useStaticQuery, type HeadFC, type PageProps, graphql, Link } from "gatsby"
import AppContent from "../components/AppContent"
import Dec from "../components/Dec"
import Head_ from "../components/Head_"

const IndexPage: React.FC<PageProps> = () => {
  const posts = useStaticQuery(graphql`
  query Query {
    allMarkdownRemark {
      nodes {
        frontmatter{
          title
          date
          id
          tags
        }
      }
    }
  }
`)

const post = posts.allMarkdownRemark.nodes.map((item:any) => 
  <div className="pt-2 dark:hover:outline-blue-400 hover:outline-blue-400 outline outline-gray-300 dark:outline-gray-800 hover:outline-2 transition-all outline-1 relative overflow-hidden rounded-xl md:pl-2 flex flex-col gap-1 items-center md:items-start pb-2 select-none" key={item.frontmatter.id}>
  {item.frontmatter.title&&<Link to={"/posts/"+item.frontmatter.id} className="text-lg z-[1] transition-all font-semibold dark:text-gray-50">{item.frontmatter.title}</Link>||<div>null</div>}
  <div className=" dark:text-gray-400 text-gray-700 flex flex-col gap-1 z-[1]">
  {item.frontmatter.tags&&<div className="flex flex-row gap-2 md:text-start text-center">{item.frontmatter.tags.map((item_:any,i:Number)=><Link key={item.frontmatter.id+'-'+item_.text+'-'+i} className="hover:text-gray-900 dark:hover:text-gray-100 transition-all" to={"/tags/"+item_}>🏷️&nbsp;{item_}</Link>)}</div>||<div>null</div>}
  {item.frontmatter.date&&<div className="text-center md:text-start">{item.frontmatter.date}</div>||<div>null</div>}
  </div>
  </div>)
  return (
    <AppContent setTheme={undefined}>
      <div className="word-break break-all"><Dec /></div>
      <div className="pt-4 gap-3 w-[98%] grid md:grid-cols-2 lg:grid-cols-3 grid-cols-1">
        {post}
      </div>
    </AppContent>
  )
}

export default IndexPage

export const Head: HeadFC = () =>  <><title>首页</title><Head_ /><script dangerouslySetInnerHTML={{__html:`if (localStorage.ti == undefined && window.matchMedia('(prefers-color-scheme:dark)').matches || localStorage.ti == 1 && window.matchMedia('(prefers-color-scheme:dark)').matches) {document.documentElement.classList.add('dark')} else if (localStorage.ti==0){document.documentElement.classList.add('dark')}`}}></script></>
