import { graphql, useStaticQuery } from "gatsby"
import React from "react"

const Dec = () => {
    const data = useStaticQuery(graphql`
    query Query_1 {
      site {
        siteMetadata {
          description
        }
      }
    }
  `)
  
    return (
      <div className="text-lg dark:text-gray-50 font-semibold text-center select-none">{data.site.siteMetadata.description}</div>
    )
  } 

  export default Dec